import React from "react";
import { connect } from "react-redux";

import { StoreState } from "../../reducers";
// import VideoCardSupervisor from "./VideoCardSupervisor";
import ProctoringMainTopBar from "../../components/ProctoringMainTopBar/ProctoringMainTopBar";
import ProctoringStatistics from "../../components/ProctoringStatistics/ProctoringStatistics";
import ProctoringCandidateDetails from "../../components/ProctoringCandidateDetails/ProctoringCandidateDetails";
import ProctoringEvents from "../../components/ProctoringEvents/ProctoringEvents";
import ProctoringCandidates from "../../components/ProctoringCandidates/ProctoringCandidates";
import CenterCardSupervisor from "../../components/CenterCardSupervisor/CenterCardSupervisor";
import {
  CandidateInterface,
  RoomServer,
  EventInterface,
  OnlineCandidates,
  Events,
  ExamEvents,
  CandidateProctoringInterface,
} from "../../interfaces";
// import { eventDefiner, eventDefinerInterface } from "../../actions/proctoring";
import {
  Socket,
  FC_GetProctoringRoom,
  Auth,
  Proctoring,
  FC_SetActiveCandidates,
  FC_SetOneActiveCandidate,
  FC_CandidateOffline,
  FC_ExamStartStop,
  FC_ContinueExam,
  FC_StopCandidate,
  FC_CandidateExamEvent,
} from "../../actions";
// props for the component
export interface ProctoringOnlineSupervisorProps {
  socket: Socket;
  auth: Auth;
  proctoring: Proctoring;
  candidate: CandidateInterface;
  server_room: RoomServer;
  FC_GetProctoringRoom: Function;
  FC_SetActiveCandidates: Function;
  FC_SetOneActiveCandidate: Function;
  FC_CandidateOffline: Function;
  FC_ExamStartStop: Function;
  FC_ContinueExam: Function;
  FC_StopCandidate: Function;
  FC_CandidateExamEvent: Function;
}

enum ActiveSideTap {
  Candidates,
  Events,
}

interface AppState {
  me: CandidateInterface;
  loading: boolean;
  loading_room: boolean;
  candidates: CandidateInterface[];
  events: EventInterface[];
  active_side_tab: ActiveSideTap;
  selected_candidate: string | null;
  online_room: boolean;
  online_candidates: OnlineCandidates[];
  start_exam: boolean;
  error: {
    target: string;
    msg: string;
  };
}

class _ProctoringOnlineSupervisor extends React.Component<
  ProctoringOnlineSupervisorProps,
  AppState
> {
  private myPeer: any;

  constructor(props: ProctoringOnlineSupervisorProps) {
    super(props);

    this.state = {
      loading_room: true,
      me: this.props.candidate,
      loading: false,
      candidates: [],
      events: [],
      active_side_tab: ActiveSideTap.Events,
      selected_candidate: null,
      online_room: false,
      online_candidates: [],
      start_exam: false,
      error: {
        msg: "",
        target: "",
      },
    };
  }

  componentDidMount = () => {
    // load candidates
    this.props.FC_GetProctoringRoom(
      "",
      (status: boolean, candidates: any[], msg: string) => {
        if (status === true) {
          this.setState({
            loading_room: false,
          });

          //

          //* add the user to the list
          this.props.socket.socket.emit(
            Events.JOIN_ROOM,
            {
              room_id: this.props.proctoring.room.room_id,
              user_id: this.props.auth.user.user_id,
              peer_id: "",
            },
            (active_candidates: OnlineCandidates[]) =>
              this.props.FC_SetActiveCandidates(active_candidates)
          );
        } else {
          this.setState({
            loading_room: false,
            error: {
              target: "all",
              msg: msg,
            },
          });
        }
      }
    );

    // center room proctoring
    //* New user connected
    this.props.socket.socket.on(
      Events.USER_CONNECTED,
      (new_user: OnlineCandidates) => {
        console.log({
          new_joined_user: new_user,
        });
        this.props.FC_SetOneActiveCandidate(new_user.user_id);
      }
    );

    //* remove the candidate online
    this.props.socket.socket.on(Events.USER_DISCONNECTED, (userId: any) =>
      this.props.FC_CandidateOffline(userId)
    );

    /**
     * @description Exam events
     */
    this.props.socket.socket.on(
      ExamEvents.CAND_EXAM_EVENT_CLIENT,
      (event: EventInterface) => this.props.FC_CandidateExamEvent(event)
    );
  };

  componentWillUnmount = () => {
    // this.props.socket.socket.off(ExamEvents.START_EXAM_CLIENT);
    this.props.socket.socket.off(Events.USER_CONNECTED);
    this.props.socket.socket.off(Events.USER_DISCONNECTED);
    this.props.socket.socket.off(ExamEvents.CAND_EXAM_EVENT_CLIENT);
  };

  startCenterProctoring = () => {};

  sendEvent = () => {
    console.log("Sending a signal");
    this.props.socket.socket.emit(ExamEvents.CAND_WARNING_EXAM_SERVER, {
      user_id: this.props.auth.user.user_id,
      room_id: this.props.server_room.room_id,
    });
  };
  // ***************************************************************************

  blank = (e: any) => console.log("clicked");

  /**
   * chand the active tab on the side
   * @param itm
   * @returns
   */
  ActiveSIdeTabHandler = (itm: ActiveSideTap) =>
    this.setState({ active_side_tab: itm });

  SelectCandidateHandler = (value: string | null) =>
    this.setState({ selected_candidate: value });

  startExamHandler = () => {
    this.props.socket.socket.emit(ExamEvents.START_EXAM_SERVER, {
      room_id: this.props.proctoring.room.room_id,
      data: "start exam",
    });
    this.props.FC_ExamStartStop(true);
  };

  stopExamHandler = () => {
    this.props.socket.socket.emit(ExamEvents.STOP_EXAM_SERVER, {
      room_id: this.props.proctoring.room.room_id,
    });

    this.props.FC_ExamStartStop(false);
  };

  /**
   * @description Enable the candidate to continue the exam
   * @param user_id
   */
  continueHandler = (user_id: string) => {
    this.props.FC_ContinueExam(user_id);
    this.props.socket.socket.emit(ExamEvents.CAND_CONTINUE_EXAM_SERVER, {
      room_id: this.props.proctoring.room.room_id,
      user_id: user_id,
    });
  };

  /**
   * @description Enable the candidate to continue the exam
   * @param user_id
   */
  stopCandidateHandler = (user_id: string) => {
    this.props.socket.socket.emit(ExamEvents.CAND_STOP_EXAM_SERVER, {
      room_id: this.props.proctoring.room.room_id,
      user_id: user_id,
      msg: "Hey you are stopped by the supervisor",
    });

    this.props.FC_StopCandidate(user_id);
  };

  /**
   * @description Enable the candidate to continue the exam
   * @param user_id
   */
  warnCandidateHandler = (user_id: string) => {
    this.props.socket.socket.emit(ExamEvents.CAND_WARNING_EXAM_SERVER, {
      room_id: this.props.proctoring.room.room_id,
      user_id: user_id,
      msg: "yey you are fucked",
    });
  };

  /**
   * @description Enable the candidate to continue the exam
   * @param user_id
   */
  restartCandidateHandler = (user_id: string) => {
    console.log("RESTART EXAM CANDIDATE");
    this.props.socket.socket.emit(ExamEvents.CAND_RESTART_EXAM_SERVER, {
      room_id: this.props.proctoring.room.room_id,
      user_id: user_id,
      msg: "You did it again",
    });
  };

  render() {
    let activeCandidates = this.props.proctoring.candidates.filter(
      (itm: CandidateProctoringInterface) => itm.active === true
    );

    let nonActiveCandidates = this.props.proctoring.candidates.filter(
      (itm) => itm.active === false
    );

    return (
      <div className="fixed right-0 left-0 bottom-0 top-0">
        <ProctoringMainTopBar
          course_title="12234231 - Mathematics for enginners"
          post_title="Math and physics form engi"
          duration="123"
          started_at="12:00 AM"
          end_at="14:00 PM"
          remaining="1:25:13"
          exam_start={this.props.proctoring.room.start_exam}
          startExamHandler={this.startExamHandler}
          stopExamHandler={this.stopExamHandler}
        />

        {/* this is the body */}
        <div className=" proctoring-body-contents h-screen text-white">
          <div className="grid --grid-col-3 lg:grid-flow-col gap-4">
            <div className="col-span-4 p-2 pt-3 h-screen overflow-y-auto">
              {/* pictures or images */}
              <div className="">
                <h3 className="text-3xl font-bold text-blue-400 pb-1 mb-1">
                  Non active candidates{" "}
                  <span className="inline-block mr-1 p-1 bg-green-700 text-white text-md py-0 px-2 rounded-full">
                    {activeCandidates.length}
                  </span>
                </h3>

                <div className="grid grid-flow-row sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-4 2xl:grid-cols-5 gap-4">
                  {activeCandidates.map(
                    (candidate: CandidateProctoringInterface, i: number) => {
                      return (
                        <CenterCardSupervisor
                          key={i}
                          seat_number={i + 1}
                          candidate={candidate}
                          stopHandler={this.stopCandidateHandler}
                          warningHandler={this.warnCandidateHandler}
                          detailsHandler={this.SelectCandidateHandler}
                          continueHandler={this.continueHandler}
                          exam_started={this.props.proctoring.room.start_exam}
                        />
                      );
                    }
                  )}
                </div>
                <h3 className="mt-9 text-3xl font-bold text-blue-400 pb-1">
                  Non active candidates{" "}
                  <span className="inline-block mr-1 p-1 bg-green-700 text-white text-md py-0 px-2 rounded-full">
                    {nonActiveCandidates.length}
                  </span>
                </h3>
                <div className="mt-3">
                  <ProctoringCandidates
                    candidates={nonActiveCandidates}
                    clickHandler={this.SelectCandidateHandler}
                  />
                </div>
              </div>
            </div>
            <div className="p-2 proctoring-side-section col-span-1 transition-all ease-in-out  h-screen overflow-y-auto  border-l border-blue-500 border-opacity-30">
              {/* statistics */}
              {this.state.selected_candidate === null ? (
                <React.Fragment>
                  <ProctoringStatistics
                    total={43}
                    active={43}
                    done={43}
                    left={43}
                  />

                  {/* candidatetabs */}

                  {/* {this.state.online_room === true && ( */}
                  <div className="my-2 p-1 rounded-md bg-blue-800 bg-opacity-60 grid grid-col-2 grid-flow-col gap-1">
                    <button
                      onClick={this.ActiveSIdeTabHandler.bind(
                        this,
                        ActiveSideTap.Events
                      )}
                      className={`btn rounded-md bg-blue-700 text-blue-100 ${
                        this.state.active_side_tab === ActiveSideTap.Events
                          ? "border-2 border-blue-400"
                          : "bg-blue-800 bg-opacity-60"
                      }`}
                    >
                      Event list
                    </button>

                    <button
                      onClick={this.ActiveSIdeTabHandler.bind(
                        this,
                        ActiveSideTap.Candidates
                      )}
                      className={`btn rounded-md bg-blue-700 text-blue-100 ${
                        this.state.active_side_tab === ActiveSideTap.Candidates
                          ? "border-2 border-blue-400"
                          : "bg-blue-800 bg-opacity-60"
                      }`}
                    >
                      Candidates
                    </button>
                  </div>
                  {/* )} */}

                  {/* events */}
                  {this.state.active_side_tab === ActiveSideTap.Events ? (
                    <ProctoringEvents
                      candidates={this.props.proctoring.candidates}
                      events={this.state.events}
                    />
                  ) : (
                    <ProctoringCandidates
                      candidates={this.props.proctoring.candidates}
                      clickHandler={this.SelectCandidateHandler}
                    />
                  )}
                </React.Fragment>
              ) : (
                <ProctoringCandidateDetails
                  candidate={this.props.proctoring.candidates.find(
                    (itm) => itm.user_id === this.state.selected_candidate
                  )}
                  events={this.state.events.filter(
                    (evnt: EventInterface) =>
                      evnt.user_id === this.state.selected_candidate
                  )}
                  chatHandler={this.blank}
                  micHandler={this.blank}
                  focusHandler={this.blank}
                  stopHandler={this.stopCandidateHandler}
                  restartHandler={this.restartCandidateHandler}
                  warnHandler={this.warnCandidateHandler}
                  closeHandler={() => this.SelectCandidateHandler(null)}
                  home_proctoring={false}
                  exam_started={this.props.proctoring.room.start_exam}
                />
              )}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  socket,
  auth,
  proctoring,
}: StoreState): { socket: Socket; auth: Auth; proctoring: Proctoring } => {
  return { socket: socket, auth: auth, proctoring: proctoring };
};

export const ProctoringOnlineSupervisor = connect(mapStateToProps, {
  FC_GetProctoringRoom,
  FC_SetActiveCandidates,
  FC_SetOneActiveCandidate,
  FC_CandidateOffline,
  FC_ExamStartStop,
  FC_ContinueExam,
  FC_StopCandidate,
  FC_CandidateExamEvent,
})(_ProctoringOnlineSupervisor);
