import React, { FC, ReactElement } from "react";
import { MdNotInterested, MdWarning } from "react-icons/md";
import { CandidateProctoringInterface } from "../../interfaces";

type CenterCardSupervisorProps = {
  seat_number: number;
  candidate: CandidateProctoringInterface;
  warningHandler: Function;
  stopHandler: Function;
  detailsHandler: Function;
  continueHandler: Function;
  exam_started: boolean;
};

const CenterCardSupervisor: FC<CenterCardSupervisorProps> = ({
  seat_number,
  candidate,
  warningHandler,
  stopHandler,
  detailsHandler,
  continueHandler,
  exam_started,
}): ReactElement => {
  /* function body */
  let color = candidate?.stopped === true ? "red" : "blue";
  return (
    <div
      className={`bg-${color}-900 bg-opacity-50 hover:bg-opacity-100 cursor-pointer border-${color}-400 border p-2 rounded-md`}
      onClick={detailsHandler.bind(this, candidate.user_id)}
    >
      <div className="text-center">
        <span className={`text-${color}-200`}>Seat number</span>
        <h1 className="text-5xl font-bold">{seat_number}</h1>
      </div>

      <div className={`mt-1 border-t border-${color}-500 pt-2`}>
        <h4 className={`text-xs text-${color}-400`}>Full names</h4>
        <h4 className="text-sm font-bold">
          {candidate.fname} {candidate.lname}
        </h4>
      </div>

      {exam_started === true && (
        <div className="mt-2">
          {candidate.stopped === false ? (
            <button
              onClick={warningHandler.bind(this, candidate.user_id)}
              className="px-2 text-sm py-1 rounded-md text-gray-900 bg-yellow-300"
            >
              <MdWarning className="inline" /> Warn
            </button>
          ) : (
            <button
              onClick={continueHandler.bind(this, candidate.user_id)}
              className="px-2 text-sm py-1 rounded-md text-gray-900 bg-green-400"
            >
              <MdWarning className="inline" /> Condinue
            </button>
          )}
          <button
            onClick={stopHandler.bind(this, candidate.user_id)}
            className="px-2 text-sm py-1 rounded-md text-white bg-red-700 ml-1"
          >
            <MdNotInterested className="inline" /> Stop
          </button>
        </div>
      )}
    </div>
  );
};
export default CenterCardSupervisor;
