import React, { FC, ReactElement, useState } from "react";
import { CandidateProctoringInterface, EventInterface } from "../../interfaces";
import ProctoringEvents from "../ProctoringEvents/ProctoringEvents";
import {
  MdAccountCircle,
  MdArrowBack,
  MdVolumeUp,
  MdChat,
  MdFullscreen,
  MdNotInterested,
  MdReplay,
  MdWarning,
} from "react-icons/md";

type ProctoringCandidateDetailsProps = {
  candidate: CandidateProctoringInterface | undefined;
  chatHandler: Function;
  micHandler: Function;
  focusHandler: Function;
  stopHandler: Function;
  restartHandler: Function;
  warnHandler: Function;
  closeHandler: Function;
  events: EventInterface[];
  home_proctoring: boolean;
  exam_started: boolean;
};

enum ActiveTap {
  Details,
  Events,
}

const ProctoringCandidateDetails: FC<ProctoringCandidateDetailsProps> = ({
  candidate,
  chatHandler,
  micHandler,
  focusHandler,
  stopHandler,
  restartHandler,
  warnHandler,
  closeHandler,
  events,
  home_proctoring,
  exam_started,
}): ReactElement => {
  const [active, setActive] = useState(ActiveTap.Details);

  if (candidate === undefined) {
    return (
      <div className="p-4 text-center">
        <h1 className="text-3xl text-blue-400">Opps!!</h1>
        <p className="text-3xl text-blue-400">
          The selected candidate can't be found{" "}
        </p>
      </div>
    );
  }

  return (
    <div className="p-1">
      <div className="p-2 border-rounded bg-blue-800 rounded-md flex items-baseline">
        <button
          onClick={() => closeHandler()}
          className="p-1 block bg-blue-900 bg-opacity-70 rounded-md mr-2 font-bold text-1xl"
        >
          <MdArrowBack />
        </button>
        <h3 className="text-center -mt-1 mb-0 pb-0 text-1xl font-bold block text-blue-500">
          User details
        </h3>
      </div>
      <div className="p-2 text-center">
        <MdAccountCircle className="text-9xl text-center mx-auto" />

        <h1 className="text-xl text-white">{candidate.email}</h1>
        <h2 className="text-gray-400 text-md">
          {candidate.fname} {candidate.lname}
        </h2>
      </div>
      <div className="my-2 py-1 rounded border- -border-blue-500 grid grid-cols-3 gap-1">
        {/* buttons start here */}

        {exam_started === true && candidate.active === true && (
          <>
            {home_proctoring === true && (
              <>
                <button
                  onClick={micHandler.bind(this, candidate.user_id)}
                  className="text-center bg-blue-900 rounded-md p-1 border-2 border-blue-800 hover:bg-blue-800 text-blue-200 hover:text-white"
                >
                  <MdVolumeUp className="text-2xl mx-auto b-0 pb-0" />
                  <span className="text-xs p-0 block -mt-0">Mic</span>
                </button>

                <button
                  onClick={chatHandler.bind(this, candidate.user_id)}
                  className="text-center bg-blue-900 rounded-md p-1 border-2 border-blue-800 hover:bg-blue-800 text-blue-200 hover:text-white"
                >
                  <MdChat className="text-2xl mx-auto b-0 pb-0" />
                  <span className="text-xs p-0 block -mt-0">Chat</span>
                </button>

                <button
                  onClick={focusHandler.bind(this, candidate.user_id)}
                  className="text-center bg-blue-900 rounded-md p-1 border-2 border-blue-800 hover:bg-blue-800 text-blue-200 hover:text-white"
                >
                  <MdFullscreen className="text-2xl mx-auto b-0 pb-0" />
                  <span className="text-xs p-0 block -mt-0">Focus</span>
                </button>
              </>
            )}

            <button
              onClick={stopHandler.bind(this, candidate.user_id)}
              className="text-center bg-blue-900 rounded-md p-1 border-2 border-blue-800 hover:bg-blue-800 text-blue-200 hover:text-white"
            >
              <MdNotInterested className="text-2xl mx-auto b-0 pb-0" />
              <span className="text-xs p-0 block -mt-0">Stop</span>
            </button>

            <button
              onClick={restartHandler.bind(this, candidate.user_id)}
              className="text-center bg-blue-900 rounded-md p-1 border-2 border-blue-800 hover:bg-blue-800 text-blue-200 hover:text-white"
            >
              <MdReplay className="text-2xl mx-auto b-0 pb-0" />
              <span className="text-xs p-0 block -mt-0">Restart</span>
            </button>

            <button
              title="give a warning"
              onClick={() => warnHandler(candidate.user_id)}
              className="text-center bg-blue-900 rounded-md p-1 border-2 border-blue-800 hover:bg-blue-800 text-blue-200 hover:text-white"
            >
              <MdWarning className="text-2xl mx-auto b-0 pb-0" />
              <span className="text-xs p-0 block -mt-0">Warn</span>
            </button>
          </>
        )}
      </div>

      <div className="my-1 bg-blue-900 bg-opacity-40 rounded-md p-1 mt-2 grid grid-cols-2 gap-2 text-sm">
        <button
          onClick={() => setActive(ActiveTap.Details)}
          className={`p-1 rounded-md text-white ${
            active === ActiveTap.Details
              ? "bg-blue-700 border-2 border-blue-300"
              : "bg-blue-900"
          }`}
        >
          candidate
        </button>

        <button
          onClick={() => setActive(ActiveTap.Events)}
          className={`p-1 rounded-md text-white ${
            active === ActiveTap.Events
              ? "bg-blue-700 border-2 border-blue-300"
              : "bg-blue-900"
          }`}
        >
          Events
        </button>
      </div>

      {active === ActiveTap.Details ? (
        <div className="mt-2">
          <h2 className="text-blue-500 opacity-40 font-bold text-1xl mb-1">
            User information
          </h2>
          <div className="p-2 rounded-md border border-blue-400 border-opacity-20">
            {/* details start from here */}
            <div className="border-b border-blue-400 border-opacity-60 py-1">
              <label className="text-sm text-blue-400 m-0 p-0">Username:</label>
              <h4 className="text-white font-bold text-lg p-0 -mt-1">
                {candidate.email}
              </h4>
            </div>
            <div className="border-b border-blue-400 border-opacity-60 py-1">
              <label className="text-sm text-blue-400 m-0 p-0">
                First name:
              </label>
              <h4 className="text-white font-bold text-lg p-0 -mt-1">
                {candidate.fname}
              </h4>
            </div>
            <div className="border-b border-blue-400 border-opacity-60 py-1">
              <label className="text-sm text-blue-400 m-0 p-0">
                Last name:
              </label>
              <h4 className="text-white font-bold text-lg p-0 -mt-1">
                {candidate.lname}
              </h4>
            </div>
            <div className="border-b border-blue-400 border-opacity-60 py-1">
              <label className="text-sm text-blue-400 m-0 p-0">email:</label>
              <h4 className="text-white font-bold text-lg p-0 -mt-1">
                {candidate.email}
              </h4>
            </div>
          </div>
        </div>
      ) : (
        <ProctoringEvents candidates={[candidate]} events={events} />
      )}
    </div>
  );
};
export default ProctoringCandidateDetails;
